/* Animation for my name */ 

.name span {
    color: #fcceb1;
    cursor: pointer;
	display: inline-block;
	font-size: 3.3rem;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	line-height: .8;
}

.name {
	font-family: 'Poppins', sans-serif;
	font-weight: 900;
	perspective: 1000px;
	white-space: nowrap;
}

.name span:nth-child(1).active {
	-webkit-animation: balance 1.2s ease-out;
	        animation: balance 1.2s ease-out;
	transform-origin: bottom left;
}

#developer {
	width: 400px;
}

@-webkit-keyframes balance {
	0%, 100% {
		transform: rotate(0deg);
	}
	
	30%, 60% {
		transform: rotate(-45deg);
	}
}

@keyframes balance {
	0%, 100% {
		transform: rotate(0deg);
	}
	
	30%, 60% {
		transform: rotate(-45deg);
	}
}

.name span:nth-child(2).active {
	-webkit-animation: shrinkjump 1s ease-in-out;
	        animation: shrinkjump 1s ease-in-out;
	transform-origin: bottom center;
}

@-webkit-keyframes shrinkjump {
	10%, 35% {
		transform: scale(2, .2) translate(0, 0);
	}
	
	45%, 50% {
		transform: scale(1) translate(0, -150px);
	}
	
	80% {
		transform: scale(1) translate(0, 0);
	}
}

@keyframes shrinkjump {
	10%, 35% {
		transform: scale(2, .2) translate(0, 0);
	}
	
	45%, 50% {
		transform: scale(1) translate(0, -150px);
	}
	
	80% {
		transform: scale(1) translate(0, 0);
	}
}

.name span:nth-child(3).active {
	-webkit-animation: falling 1s ease-out;
	        animation: falling 1s ease-out;
	transform-origin: bottom center;
}

@-webkit-keyframes falling {
	12% {
		transform: rotateX(240deg);
	}
	
	24% {
		transform: rotateX(150deg);
	}
	
	36% {
		transform: rotateX(200deg);
	}
	
	48% {
		transform: rotateX(175deg);
	}
	
	60%, 85% {
		transform: rotateX(180deg);
	}
	
	100% {
		transform: rotateX(0deg);
	}
}

@keyframes falling {
	12% {
		transform: rotateX(240deg);
	}
	
	24% {
		transform: rotateX(150deg);
	}
	
	36% {
		transform: rotateX(200deg);
	}
	
	48% {
		transform: rotateX(175deg);
	}
	
	60%, 85% {
		transform: rotateX(180deg);
	}
	
	100% {
		transform: rotateX(0deg);
	}
}

.name span:nth-child(4).active {
	-webkit-animation: rotate 0.8s ease-out;
	        animation: rotate 0.8s ease-out;
}

@-webkit-keyframes rotate {
	20%, 80% {
		transform: rotateY(180deg);
	}
	
	100% {
		transform: rotateY(360deg);
	}
}

@keyframes rotate {
	20%, 80% {
		transform: rotateY(180deg);
	}
	
	100% {
		transform: rotateY(360deg);
	}
}

